body {
  font-family: 'Arial', sans-serif;
  background: linear-gradient(to top left, #dbeafe, #f0fdf4);
  padding: 20px;
  margin: 0;
}
body, .dark-mode {
  background-color: #121212;
  color: #ffffff;
}

.surah-btn, .verse-btn {
  background-color: #1e1e1e;
  color: #ffe600;
  box-shadow: 0 4px 10px rgba(0,0,0,0.4);
  transition: transform 0.3s ease, opacity 0.3s ease;
}

.surah-btn:hover, .verse-btn:hover {
  transform: scale(1.05);
}

.surah-btn {
  display: inline-block;
  padding: 10px 20px;
  margin: 8px;
  border-radius: 30px;
  background-color: #1e88e5;
  color: white;
  font-weight: bold;
  border: none;
  animation: float 3s ease-in-out infinite;
  transition: transform 0.3s, background-color 0.3s;
}
.surah-btn:hover {
  background-color: #42a5f5;
  transform: scale(1.05);
}
h1, h2 {
  text-align: center;
  color: #1e3a8a;
}

.surah-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 12px;
  margin-top: 20px;
}

.surah-btn {
  padding: 12px 20px;
  background-color: #93c5fd;
  border: none;
  border-radius: 10px;
  font-weight: bold;
  cursor: pointer;
  color: #1e40af;
  transition: 0.2s ease-in-out;
  text-decoration: none;
}

.surah-btn:hover {
  background-color: #60a5fa;
}

.quiz-container {
  max-width: 900px;
  margin: 0 auto;
}

.verses-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  justify-content: center;
  margin: 20px 0;
}

.verse-btn {
  background-color: #fcd34d;
  padding: 12px;
  border: none;
  border-radius: 10px;
  font-size: 16px;
  max-width: 100%;
  min-width: 200px;
  cursor: pointer;
  box-shadow: 2px 2px 6px #888;
}

.verse-btn:hover {
  background-color: #fde68a;
}

.selected-box {
  border-top: 2px dashed #1e3a8a;
  padding-top: 10px;
  margin-top: 20px;
}

.selected-verse {
  padding: 10px;
  margin: 5px auto;
  background-color: #bbf7d0;
  border-radius: 6px;

  max-width: 700px;
}

.back-btn {
  display: block;
  margin: 30px auto 10px;
  text-align: center;
  font-size: 16px;
  color: #2563eb;
  text-decoration: none;
}

.verse-btn.error {
  animation: shake 0.3s;
  background-color: #fecaca;
}

@keyframes shake {
  0% { transform: translateX(0); }
  25% { transform: translateX(-4px); }
  50% { transform: translateX(4px); }
  75% { transform: translateX(-4px); }
  100% { transform: translateX(0); }
}

.success-msg {
  background-color: #bbf7d0;
  padding: 10px;
  border-radius: 8px;
  text-align: center;
  color: #166534;
  font-weight: bold;
  margin-top: 20px;
}

.surah-title-floating,
.title-floating {
  animation: floatTitle 6s ease-in-out infinite alternate;
  display: inline-block;
  padding: 0.5em 1.5em;
  background-color: #ffecb3;
  border-radius: 50px;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
}

@keyframes floatTitle {
  0% { transform: translateY(0px); }
  100% { transform: translateY(-10px); }
}

.verse-btn.balloon {
  transition: all 0.3s ease;
  animation: floatVerse 3s ease-in-out infinite alternate;
  border-radius: 30px;
  background-color: #d0f0fd;
  box-shadow: 0 5px 10px rgba(0,0,0,0.1);
  font-weight: bold;
}

@keyframes floatVerse {
  0% { transform: translateY(0px); }
  100% { transform: translateY(-6px); }
}

.verse-btn.error {
  background-color: #f8d7da;
  color: #721c24;
}

.selected-verse {
  margin: 5px;
  font-size: 1.1em;
}

.success-msg {
  background-color: #d4edda;
  padding: 10px;
  border-radius: 10px;
  margin-top: 20px;
  font-weight: bold;
}

.back-btn {
  margin-top: 20px;
  display: inline-block;
  padding: 10px 15px;
  background: #eee;
  border-radius: 8px;
  text-decoration: none;
}
.surah-btn.title-floating {
  display: inline-block;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 50px;
  background-color: #fdd835;
  font-weight: bold;
  box-shadow: 0 5px 15px rgba(0,0,0,0.2);
  animation: float 3s ease-in-out infinite;
  transition: transform 0.3s ease;
}

@keyframes float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-8px); }
}

.surah-btn.title-floating:hover {
  transform: scale(1.05);
}
body {
  background-color: #121212;
  color: #ffffff;
  font-family: 'Cairo', sans-serif;
  margin: 0;
  padding: 0;
}

.quiz-container, .home {
  padding: 20px;
}

h1, h2 {
  text-align: center;
  color: #ffeb3b;
  margin-bottom: 20px;
  text-shadow: 0 0 10px #ffeb3b88;
}

/* Surah Buttons (Balloon Style) */
.surah-btn {
  display: inline-block;
  padding: 10px 20px;
  margin: 8px;
  border-radius: 30px;
  background-color: #1e88e5;
  color: white;
  font-weight: bold;
  border: none;
  animation: float 3s ease-in-out infinite;
  transition: transform 0.3s, background-color 0.3s;
  cursor: pointer;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.4);
}

.surah-btn:hover {
  background-color: #42a5f5;
  transform: scale(1.05);
}

/* Verse Buttons */
.verse-btn {
  display: inline-block;
  padding: 12px 20px;
  margin: 8px;
  background-color: #4caf50;
  color: #fff;
  border: none;
  border-radius: 30px;
  cursor: pointer;
  animation: pulse 2s ease-in-out infinite;
  transition: background-color 0.3s, transform 0.2s ease;
}

.verse-btn:hover {
  background-color: #66bb6a;
  transform: scale(1.05);
}

.verse-btn.error {
  background-color: #f44336 !important;
}

.verse-btn.selected {
  background-color: #9ccc65;
  opacity: 0.5;
  pointer-events: none;
}

/* Selected verses */
.selected-box {
  margin-top: 20px;
  background-color: #1d1d1d;
  padding: 15px;
  border-radius: 12px;
  border: 1px solid #333;
}

.selected-verse {
  margin: 5px 0;
  color: black;
  font-weight: bold;
}

/* Success Message */
.success-msg {
  background-color: #43a047;
  padding: 12px;
  color: #fff;
  font-weight: bold;
  text-align: center;
  border-radius: 8px;
  margin-top: 20px;
}

/* Back Button */
.back-btn {
  display: block;
  text-align: center;
  margin-top: 20px;
  padding: 10px 15px;
  background-color: #607d8b;
  color: white;
  text-decoration: none;
  border-radius: 8px;
}

.back-btn:hover {
  background-color: #78909c;
}

/* Balloon animation for buttons */
@keyframes float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-8px); }
}

@keyframes pulse {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(4px); }
}
